.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 100px;
  max-width: 350px;
  width: 100%;
  padding: 12px;
  background-color: rgb(214, 211, 211);
  border-radius: 10px;
  box-shadow: inset -1px 0px 10px 2px grey,
    0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
.content h1 {
  margin: 0;
  color: #0c1bb8;
}
